.shippingContainer{
    width: 100vw;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.shippingBox{
    background-color: white;
    width: 25vw;
    box-sizing: border-box;
    overflow: hidden;
}
.shippingHeading{
    text-align: center;
    color: rgba(0, 0, 0, .623);
    font: 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0, 0, 0, .268);
    width: 50%;
    margin: auto;
}

.shippingForm {
    margin: auto;
display: flex;
align-items: center;
flex-direction: column;
    padding: 2vmax;
    height: 80%;
    transition: all .5s;

}



.shippingForm > div{
    display: flex;
    width: 100%;
    align-items: center;
    position: relative;
    margin-bottom: 1.5vmax;
}

.shippingForm > div > input,
.shippingForm > div > select{
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, .268);
    border-radius: 4px;
    font: 300 .9vmax cursive;
    outline: none;
}

.shippingForm > div > svg{
position: absolute;
transform: translateX(1vmax);
font-size: 1.6vmax;
color: rgba(0, 0, 0, .623);
}

.shippingBtn{
    border: none;
    background-color: #FFD814 !important;
    color: black;
    font: 300 .9vmax "Roboto";
    width: 100%;
    padding: .8vmax;
    cursor: pointer;
    transition: all .5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, .268);
}

.shippingBtn:hover{
    background-color: #febd69;
}

@media screen and (max-width:600px){
    
    .shippingContainer{
        background-color: white;
         /* Sidebar Adjustment */
         width: calc(100% - 50px);
         position: relative;
         left: 50px;
    }
.shippingBox{
width: 100%;
height: 95vh;
}

.shippingForm{
    padding: 2vmax;
}
.shippingHeading{
    font-size: 2.3vmax;
}
.shippingForm > div > input,
.shippingForm > div > select{
    padding: 2.5vmax 5vmax;
    font: 300 2.7vmax cursive;
}

.shippingForm > div > svg{
font-size: 2.8vmax;
}


.shippingBtn{
    font: 300 1.9vmax "Roboto";
    padding: 1.8vmax;
}
.eye > svg {
    font-size: 2.8vmax;
}

}