.orderSuccess{
    margin: auto;
    text-align: center;
    padding: 10vmax;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.orderSuccess > svg {
    font-size: 7vmax;
    color: #febd69;
}
.orderSuccess > p {
    font-size: 2vmax;
}
.orderSuccess > a {
     background-color: rgb(51, 51, 51);
     color: white;
     margin: 2vmax;
     padding: 1vmax 3vmax;
     cursor: pointer;
     font: 400 1vmax "Roboto";
     text-decoration: none;
}

@media screen and (max-width:600px) {
    .orderSuccess > a{
        padding: 3vw 6vw;
        font: 400 4vw "Roboto";
        margin: 2vmax;
    }
    .orderSuccess > svg{
        font-size: 20vw;
    }
    .orderSuccess > p {
        margin: 2vmax;
        font-size: 5vw;
    }
}